	const foot2 = [{name: "Raising kids", link: "https://my.gov.au/en/services/raising-kids"}, {name: "Living arrangements", link: "https://my.gov.au/en/services/living-arrangements"}, {name: "Ageing", link: "https://my.gov.au/en/services/ageing"}, {name: "Work", link: "https://my.gov.au/en/services/work"}, {name: "Education", link: "https://my.gov.au/en/services/education"}, {name: "Health and disability", link: "https://my.gov.au/en/services/health-and-disability"},]
	const foot3 = [{name: "Terms of use", link: "https://my.gov.au/en/about/terms"}, {name: "Privacy and security", link: "https://my.gov.au/en/about/privacy-and-security"}, {name: "Copyright", link: "https://my.gov.au/en/about/copyright"}, {name: "Accessibility", link: "https://my.gov.au/en/about/accessibility"}, {name: "Languages", link: "https://my.gov.au/en/languages"},]


